import additionBlock from '../components/Addition-Block'
import longButton from '../../components/my-components/LongButton.vue'
let DataFrom = {}
export default {
    components: {
        additionBlock,
        longButton
    },
    data() {
        return {
            list: [{
                name: '',
                date: '',
                content: ''
            }]
        }
    },
    methods: {
        clickButton() {
            this.Cacher.setData('additionList', {
                from: 'additionList',
                ...DataFrom
            })
            uni.navigateTo({
                url: './billAddition?from=additionList'
            })
        },
        iniPage() {
            this.Request('billAddition', {
                id: DataFrom.bill.bill.id
            }).then(res => {
                this.list = res.list.map(item => ({
                    name: item.name,
                    date: item.create_time,
                    content: item.remark,
                    id: item.id
                }))
            })
        }
    },
    onLoad(option) {
        DataFrom = this.Cacher.getData(option.from);
        this.iniPage()
    },
    onShow() {
        this.iniPage()
    }
}
